<template>
  <div class="context-list">
    <div class="list-connection">
      <ListHeader :imgSrc="require('@/assets/title-contact.png')"
        :title='title1'></ListHeader>
      <ul class="list">
        <ListItem :iconSrc="require('@/assets/contact-phone.png')"
          :conMsg='conMsg1'
        ></ListItem>
        <ListItem :iconSrc="require('@/assets/contact-mail.png')"
          :conMsg='conMsg2' 
        ></ListItem>
        <ListItem :iconSrc="require('@/assets/social-wechat.png')" 
          :conMsg='conMsg3'
        ></ListItem>
        <ListItem :iconSrc="require('@/assets/social-qq.png')" 
          :conMsg='conMsg4'
        ></ListItem>
      </ul>
    </div>
    <div class="social-connect">
      <ListHeader :imgSrc="require('@/assets/title-contact.png')"
        :title='title2'></ListHeader>
      <ul class="list">
        <a href="https://github.com/DTZ1211">
          <ListItem :iconSrc="require('@/assets/social-github.png')"
            :conMsg='conMsg5'
          ></ListItem>
        </a>
      </ul> 
    </div>
  </div>
</template>

<script>
import ListItem from "@/components/list-item";
import ListHeader from "@/components/list-header.vue";
export default {
  data: function() {
    return {
      conMsg1: "13479792617",
      conMsg2: "1711241832@qq.com",
      conMsg3: "DTZzizzzz",
      conMsg4: '1711241832',
      conMsg5: 'DTZ1211',
      title1: "联系方式",
      title2: "社交账号"
    };
  },
  components: {
    ListItem,
    ListHeader
  }
};
</script>


<style lang="less">
a{
  text-decoration: none
}
.context-list {
  width: 100%;
  margin-bottom: 30px;
}
</style>



