<template>
  <div id="app">
    <div class="resume">
      <!-- 左侧个人信息 -->
      <div class="left">
        <Brief></Brief>
        <ContextList></ContextList>
      </div>
      <!-- 右侧板块介绍 -->
      <div class="right">
        <!-- 教育背景 -->
        <Description :title='title1'>
         <div class="education">
           <span class="school">暨南大学</span>
           <span class="major">软件工程</span>
           <span class="date">2017.9 ~ 2019.6</span>
         </div>
        </Description>
        <!-- 个人技术 -->
        <Description :title='title2'>
          <div class="skill">
            <p class="left">
              1、熟练掌握Spring、SpringMVC、Mybatis、Maven、JQuery等相关的Java程序开发技术及框架
              <br>
              2、熟练掌握Mysql、SqlServer、Sqlite等基本开发常用的数据库，并且能使用基本的sql语句对数据库内的数据进行增删改查
              <br>
              3、对Idea、Eclipse、VS、HBuilder X等常用的开发工具有所熟悉，能够使用这些工具进行项目的开发工作
              <br>
              4、在校内学习期间，对App开发技术也有过相关了解，并尝试过基本的app应用开发
              <br>
              5、对Vue、Element、BootStrap等相关前端框架也有所了解，能够使用其进行基本的页面布局
            </p>
            <div class="right">
              <div class="canvas-container">
                <canvas ref="canvas" width="200" height="200"></canvas>
                <div ref="legend"></div>
              </div>
            </div>
          </div>
        </Description>
        <!-- 项目经历 -->
        <Description :title='title3'>
          <ExpItem :time='time1' :desc='desc1' :place='place1'></ExpItem>
          <ExpItem :time='time2' :desc='desc2' :place='place2'></ExpItem>
        </Description>
        <!-- 在校经历 -->
        <Description :title='title4'>
          <div class="education">
            <span class="school">南方IT学院</span>
            <span class="major">志愿者</span>
            <span class="date">2017.8 - 2017.9</span>
          </div>
          <p class="content">
            在珠海市遭受了台风天鸽的袭击之后，跟随班级中的同学对珠海市街道两旁的树木、植株进行修复工作，通过志愿者活动我们不仅收获了更深的友谊，更使我明白了即使你力量有限也可以做很多事情。
          </p>
          <div class="education">
            <span class="school">南方IT学院</span>
            <span class="major">献血活动</span>
            <span class="date">2018.10</span>
          </div>
          <p class="content">
            珠海市献血站到校举行的献血活动，受到了广大学生的踊跃参与，并且我自己也参与其中，即有益于自己的健康，也能帮助别人。
          </p>
        </Description>
        <!-- 个人评价 -->
        <Description :title='title5'>
          <p class="content">
            本人Java专业毕业，对程序开发有较浓厚的兴趣。在学校的学习生活中，我不仅仅只学到了专业技术，同时对中华传统文化也有一定的了解。
            因此，培养提升了我的为人处世和与人交往的能力，能够更加容易地融入集体。并且，在学校的两年期间还培养了我吃苦耐劳、勤学好问的能力，
            在工作上能服从安排，抗压能力强。希望贵公司能够给我一个机会，共同发展，共同进步。
          </p>
        </Description>
      </div>
    </div>
    <button class="button" @click='saveAsImage'>保存此页为图片</button>
  </div>
</template>

<script>
//导入 conponents 相关组件和 api 相关方法
import Brief from "@/components/brief.vue";
import ContextList from "@/components/context-list.vue";
import Description from "@/components/description.vue";
import PieChart from "@/api/pieChart.js";
import ExpItem from "@/components/exp-item.vue";
import html2canvas from "@/api/html2canvas.js";
import FileSaver from "file-saver";

export default {
  name: "App",
  data: function() {
    return {
      title1: "教育背景",
      title2: "个人技能",
      title3: "项目经历",
      title4: "校园经历",
      title5: "自我评价",
      time1: "2018.9 ~ 2018.11",
      time2: "2018.12 ~ 至今",
      //设置 EChart 图数值
      piePercent: {
        English: 50,
        Vue: 40,
        Java: 80,
        JavaScript: 60
      },
      desc1:
        "这是一个自己的练手项目，实现网上手机购买的一个商城。通过该项目，来复习上一学期的内容，有利于之后对Java的深入学习",
      place1: "挖宝网",
      desc2:
        "目前正在开发过程中，采用前后端分离技术。后台使用目前流行的SSM框架，前台使用VUE-CLI脚手架，由于对前端技术的经验缺失"+
        "，所以目前正在半学半做过程中",
        place2: "房屋租赁系统",
      colors: ["#fde23e", "#f16e23", "#57d9ff", "#937e88"]
    };
  },
  //设置 EChart 图属性
  mounted() {
    const myPieChart = new PieChart({
      canvas: this.$refs.canvas,
      data: this.piePercent,
      colors: this.colors,
      doughnutHoleSize: 0.5,
      legend: this.$refs.legend
    });
    myPieChart.draw();
  },
  //保存为图片的方法
  methods: {
    saveAsImage: function() {
      let resume = document.querySelector(".resume");
      html2canvas(resume).then(function(canvas) {
        canvas.toBlob(function(blob) {
          FileSaver.saveAs(blob, "Resume.png");
        });
      });
    }
  },
  //引用组件
  components: {
    Brief,
    ContextList,
    Description,
    ExpItem
  }
};
</script>

<style lang='less'>
@import url("../node_modules/reset-css/reset.css");
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
  .resume {
    width: 1247px;
    height: 1754px;
    border: 1px solid #dad8d7;
    background-color: white;
    position: relative;
    overflow: auto;
    .left {
      width: 380px;
      height: 100%;
      box-sizing: border-box;
      float: left;
      background-color: #fdfefe;
    }
    .right {
      box-sizing: border-box;
      width: 867px;
      float: right;
      padding: 0px 40px;
      p {
        font-size: 20px;
        line-height: 32px;
        color: #555;
        text-align: justify;
      }
      .skill {
        display: flex;
        margin-top: 15px;
        justify-content: space-between;
        .left {
          width: 70%;
        }
        .right {
          width: 30%;
          .canvas-container {
            margin-top: 150px;
          }
        }
      }
      .education {
        font-size: 18px;
        font-weight: bold;
        display: flex;
        justify-content: space-between;
        margin-top: 8px;
        .school {
          width: 40%;
        }
        .major {
          width: 35%;
        }
        .date {
          width: 25%;
          text-align: right;
        }
      }
    }
    .bottom {
      position: absolute;
      bottom: 40px;
      left: 50%;
      transform: translateX(-50%);
      .text {
        font-size: 18px;
      }
    }
  }
  button.button {
    position: absolute;
    top: 40px;
    left: 50%;
    margin-left: 478px;
    background-color: white;
    border: 1px solid #dad8d7;
    cursor: pointer;
    padding: 5px 8px;
    border-radius: 4px;
    &:hover {
      background-color: #f8f8f8;
    }
  }
}
</style>



